body {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-family: 'Roboto';
    background: -webkit-linear-gradient( #bbb, transparent 1px), -webkit-linear-gradient( left, #ccc, transparent 1px);
    background: -o-linear-gradient( #bbb, transparent 1px), -o-linear-gradient( left, #ccc, transparent 1px);
    background: linear-gradient( #bbb, transparent 1px), linear-gradient( 90deg, #ccc, transparent 1px);
    background-size: 16px 16px;
    background-position: center center;
}

.simplebar-scrollbar {
    background-color: #1DE9B6;
    border-radius: 7px;
    margin-right: 2px;
}

.simplebar-track.horizontal {
    display: none;
}



